<template>
    <v-container fluid>
        <v-card v-if="this.$store.state.user_event.data.userEventsData.length > 0 && this.$store.state.user_event.loadingStatus.userEventLoading == false">
            <!--<draggable v-model="myArray">-->
            <!--</draggable>-->

            <div class="blue white--text notify_card" v-for="item in this.user_events" :key="item.id">
                <v-card-row>
                    <v-card-text>
                        {{ item.text }}
                    </v-card-text>
                </v-card-row>
                <v-card-row actions>
                    <v-btn flat class="white--text" @click:native="eventAck(item.id)">Acknowledge</v-btn>
                </v-card-row>
            </div>

        </v-card>
        <div style="margin-top: 15%; display: flex;justify-content: center;" v-else>
            <img src="static/not_exist.png"
                 style="display: flex;"></img>
        </div>
        <div style="margin-top: 20px; display: flex;justify-content: center;">
            <span class="headline green--text">USER EVENT?</span>
        </div>
        <div style="margin-top: 10px; display: flex;justify-content: center;" v-for="item in chooseWords()" :key="item.id">
            <span class="grey--text text--darken-1">{{item.words}}&nbsp;<span
                    class="blue--text text--darken-1">@{{item.by}}</span></span>
        </div>
    </v-container>

</template>
<style>
    .notify_card {
        margin-top: 2px;
        margin-bottom: 2px;
    }

    .list-complete-item {
    }

    .list-complete-enter-active {
        overflow: hidden;
        transition: all 1s;
    }

    .list-complete-leave-active {
        margin-top: 0px;
        overflow: hidden;
        transition: all 1s;
    }

    .list-complete-enter, .list-complete-leave-to {
        height: 0px;
        opacity: 0;
        padding: 0px;
        margin-top: 0px;
        overflow: hidden;
    }

</style>
<script>
  import shared from '@/shared.js'
  import draggable from 'vuedraggable'


  export default {
    name: 'Splash',
    components: {
      draggable,
    },
    computed: {
      user_events () {
        return this.$store.state.user_event.data.userEventsData;
      }
    },
    methods: {
      eventAck(item) {
        alert(item)
      },
      chooseWords() {
        const choices = this.niceWords;
        const index = Math.floor(Math.random() * choices.length);
        return [choices[index]];
      }
    },
    mounted() {
      shared.eventBus.$emit('nav-changed', "index");
      this.$store.dispatch('getUserEvents');
    },
    data () {
      return {
        niceWords: [{
          words: '自从用了莪，饭也香了，大便也通畅了，上楼梯也不喘了',
          by: 'ntr君'
        },
          {
            words: '假如生活不如意，就往头上带点绿',
            by: 'ntr君'
          }
        ]
      }
    }
  }
</script>


